                            <p>昨日销售总额</p>
                            <p>{{allList.lastDaySaleMoney}}</p>
                        </div>
                        <div><i class="el-icon-s-claim"></i></div>
                    </li>
                </ul>
            </section>
            <section class="clearfix overView">
                <div class="fl">
                    <p>商品总览</p>
                    <ul>
                        <li>
                            <div><i class="el-icon-sold-out"></i></div>
