                    <!--<span class="demonstration">选择时间范围</span>-->
                    <!--<el-date-picker-->
                            <!--v-model="value"-->
                            <!--type="monthrange"-->
                            <!--unlink-panels-->
                            <!--range-separator="至"-->
                            <!--start-placeholder="开始月份"-->
                            <!--end-placeholder="结束月份"-->
                            <!--:picker-options="pickerOptions">-->
                    <!--</el-date-picker>-->
                <!--</div>-->
                <!--<el-button type="primary" @click="test">立即修改</el-button>-->
            </section>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'index',
        data(){
            return {
                allList:{},
                moneyList:[],
                pickerOptions: {
                    shortcuts: [{
                        text: '本月',
                        onClick(picker) {
                            picker.$emit('pick', [new Date(), new Date()]);
                        }
                    }, {
                        text: '今年至今',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(new Date().getFullYear(), 0);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近六个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setMonth(start.getMonth() - 6);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                value: ''
            }
        },
        methods:{
            test:function () {
                this.$axios.post('/api/role/list',{
                        "page": 1,
                        "pageSize":6,
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
//                    this.moneyList = res.data.data;
                    this.$message.success(res.data.msg);
                })
            }
        },
        mounted:function(){
            this.$axios.post('/api/all/statistics',
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            ).then((res) => {
                    console.log(res);
                    this.allList = res.data.data;
                    this.$message.success(res.data.msg);
                });

        }
    }
</script>

