<template>
    <div id="order">
        订单列表
        <el-button type="primary" @click="addView">添加订单</el-button>
        <el-dialog
                title="添加订单"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="总价">
                    <el-input v-model="form.orderPrice"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="form.status" placeholder="请选择状态">
                        <el-option label="未付款" value="0"></el-option>
                        <el-option label="已付款" value="1"></el-option>
                        <el-option label="等待发货" value="2"></el-option>
                        <el-option label="已发货" value="3"></el-option>
                        <el-option label="运输中" value="4"></el-option>
                        <el-option label="已完成" value="5"></el-option>
                        <el-option label="已取消" value="6"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="支付类型">
                    <el-select v-model="form.payType" placeholder="请选择支付类型">
                        <el-option label="未支付" value="0"></el-option>
                        <el-option label="支付宝" value="1"></el-option>
                        <el-option label="微信" value="2"></el-option>
                        <el-option label="银联" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="add">立即增加</el-button>
                    <el-button @click="quxiao">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-table
                :data="orderList"
                height="550"
                border
                style="width: 100%">
            <!--<el-table-column-->
                    <!--label="商品图片"-->
                    <!--width="180">-->
                <!--<template slot-scope="scope">-->
                    <!--<img :src="scope.row.goodsImgs" alt="">-->
                <!--</template>-->
            <!--</el-table-column>-->
            <el-table-column
                    prop="orderCode"
                    label="订单号"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="orderPrice"
                    label="总价">
            </el-table-column>
            <el-table-column
                    label="状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.status==1">已付款</span>
                    <span v-else-if="scope.row.status==0">未付款</span>
                    <span v-else-if="scope.row.status==2">等待发货</span>
                    <span v-else-if="scope.row.status==3">已发货</span>
                    <span v-else-if="scope.row.status==4">运输中</span>
                    <span v-else-if="scope.row.status==5">已完成</span>
                    <span v-else-if="scope.row.status==6">已取消</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="支付类型">
                <template slot-scope="scope">
                    <span v-if="scope.row.payType==1">支付宝</span>
                    <span v-else-if="scope.row.payType==2">微信</span>
                    <span v-else-if="scope.row.payType==3">银联</span>
                    <span v-else>未支付</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <!--<el-button-->
                            <!--size="mini"-->
                            <!--@click="handleEdit(scope.row.id,scope.row.goodsName,scope.row.goodsPrice,scope.row.goodsScore,scope.row.status,scope.row.recommend,scope.row.goodsDetail)">编辑</el-button>-->
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                    background
                    layout="prev, pager, next, jumper, ->, total"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="current"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'order',
        data(){
            return {
                orderList:[],
                total:0,//总条数
                current:1,//当前页
                pageSize:5,//每页条数
                dialogVisible: false,
                form: {
                    orderPrice:'',
                    status: '',
                    payType: '',

                }
            }
        },
        methods:{
            //模态框设置
            addView:function () {
                this.dialogVisible=true;
            },
            handleClose() {
                this.dialogVisible= false
            },
            quxiao:function () {
                this.dialogVisible = false;
            },
            //添加订单
            add:function () {
                var price = this.form.orderPrice;
                var payType = this.form.payType;
                var status = this.form.status;
                this.$axios.post('/api/order/add', {
                        "order": {
                            "id": "string",
                            "memberId": "string",
//                            "orderCode": "string",
                            "orderPrice": price,
                            "payType": payType,
                            "status": status}
                            ,
                        "orderGoods": [
                            {
                                "goodsCount": 0,
                                "goodsId": "string",
                                "id": "string",
                                "orderId": "string"
                            }
                        ]
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                });
                this.dialogVisible=false;

            },
            //分页设置
            handleCurrentChange(val) {
                var pageSize = this.pageSize;
                this.$axios.post('/api/order/list', {
                        "page": val,
                        "pageSize":pageSize,
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                        console.log(res);
                        this.orderList = res.data.data.list;
                        this.total = res.data.data.total;
                    }
                );
                console.log(`当前页: ${val}`);
            },
            //删除订单
            handleDelete(id) {
                this.$axios.post('/api/order/delete', {
                        id:id
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            }
        },
        mounted:function () {
            this.$axios.post('/api/order/list', {

                    "page": 1,// 当前页-必传
                    "pageSize": 5,// 页容量-必传
                    "payType": -1,// 支付类型,详见支付类型接口-必传
                    "status": -1// 订单状态,详见订单状态列表接口-必传
                },
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            ).then((res) => {
                console.log(res);
                this.orderList = res.data.data.list;
                console.log(this.orderList);
                this.total = res.data.data.total;
                this.pageSize = res.data.data.pageSize;
                this.$message.success(res.data.msg);
            })
        }
    }
</script>

<style scoped>

