<template>
    <div id="memberList">
        会员列表
        <input type="text" v-model="searchName">会员状态
        <select name="" id="" v-model="searchStatus">
            <option value="1" selected>正常</option>
            <option value="0">锁定</option>
        </select>手机号
        <input type="text" v-model="searchTel">
        <button @click="search">查询</button>
        <el-button type="primary" @click="view">添加会员</el-button>
        <el-dialog
                title="添加会员"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
                <p>姓名
                <el-input v-model="user" placeholder="请输入姓名" clearable></el-input></p>
                <p>电话
                <el-input placeholder="请输入电话" v-model="tel" clearable></el-input></p>会员状态

                <el-select v-model="status" placeholder="请选择会员状态">
                    <el-option label="正常" value="1"></el-option>
                    <el-option label="禁用" value="0"></el-option>
                </el-select>

                <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="add">保存</el-button>
                <el-button type="button" @click="quxiao">取消</el-button>
                </span>
        </el-dialog>
        <el-table
                :data="memberList"
                height="550"
                border
                style="width: 100%">
            <el-table-column
                    label="头像"
                    width="180">
                <template slot-scope="scope">
                    <img :src="scope.row.memberHeader" alt="">
                </template>
            </el-table-column>
            <el-table-column
                    prop="memberName"
                    label="姓名"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="memberPhone"
                    label="电话">
            </el-table-column>

                <el-table-column
                        label="状态">
                    <template slot-scope="scope">
                    <span v-if="scope.row.status">正常</span>
                    <span v-else>禁用</span>
                    </template>
                </el-table-column>
            <el-table-column
                    label="创建时间">
                <template slot-scope="scope">
                    <span>{{scope.row.createTime | dateFormat}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.row.id,scope.row.status)">改变状态</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                    background
                    layout="prev, pager, next, jumper, ->, total"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="current"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'memberList',
        data(){
            return {
                memberList:[],
                user:"",
                tel:"",
                status:"",
                total:0,//总条数
                current:1,//当前页
                pageSize:3,//每页条数
                searchName:"",
                searchStatus:"",
                searchTel:"",
                dialogVisible: false
            }
        },
        methods:{
            //分页设置
            handleClose() {
                this.dialogVisible= false
            },
            view:function () {
                this.dialogVisible = true;
            },
            quxiao:function () {
                this.dialogVisible = false;
            },
            //修改会员状态
            handleEdit(id,status) {
                var Status;
                if(status==1){
                    Status = 0;
                }else {
                    Status = 1;
                }
                this.$axios.post('/api/member/edit', {
                        id:id,
                        status:Status
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //删除
            handleDelete(id) {
                this.$axios.post('/api/member/delete', {
                        id:id
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //添加
            add:function () {
                var user = this.user;
                var tel = this.tel;
                var status = this.status;
                this.$axios.post('/api/member/add', {
                        "isDelete": 0,
                        "memberHeader": "https://dss2.bdstatic.com/6Ot1bjeh1BF3odCf/it/u=504374624,2916877857&fm=74&app=80&f=PNG&size=f121,121?sec=1880279984&t=ae67486d63c13887708207dc87cb6b84",
                        "memberName": user,
                        "memberPhone": tel,
                        "openId": "string",
                        "status": status
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //分页
            handleCurrentChange(val) {
                var pageSize = this.pageSize;
                this.$axios.post('/api/member/list', {
                        "page": val,
                        "pageSize":pageSize,
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                        console.log(res);
                        this.memberList = res.data.data.list;
                        this.total = res.data.data.total;
                    }
                );
                console.log(`当前页: ${val}`);
            },
            //搜索
            search:function () {
                var Name = this.searchName;
                var Status = this.searchStatus;
                var Tel = this.searchTel;
                this.$axios.post('/api/member/list', {
                        "memberName": Name,
                        "status": Status,
                        "memberPhone": Tel
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                        console.log(res);
                        this.memberList = res.data.data.list;
                        this.total = res.data.data.total;
                        this.pageSize = res.data.data.pageSize;
                    }
                );
            },
        },
        mounted:function(){
            this.$axios.post('/api/member/list', {
                    "page": 1,
                    "pageSize":4,
                },
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            ).then((res) => {
                console.log(res);
                this.memberList = res.data.data.list;
                this.total = res.data.data.total;
                console.log(this.total);
                this.pageSize = res.data.data.pageSize;
                this.$message.success(res.data.msg);
            }
        )
    }
    }
</script>

<style scoped>
#memberList {
    position: relative;
}
#addMember p {
    width: 400px;
}
img {
    width: 70px;
}
</style>
