<template>
    <div id="productList">
        商品列表
        <el-dialog
                title="商品修改"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="商品名称">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格">
                <el-input v-model="form.price"></el-input>
            </el-form-item>
            <el-form-item label="商品积分">
                <el-input v-model="form.score"></el-input>
            </el-form-item>
            <el-form-item label="商品状态">
                <el-select v-model="form.status" placeholder="请选择商品状态">
                    <el-option label="上架" value="1"></el-option>
                    <el-option label="下架" value="0"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="是否推荐">
                <el-select v-model="form.recommend" placeholder="请选择是否推荐">
                    <el-option label="推荐" value="1"></el-option>
                    <el-option label="不推荐" value="0"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="商品详情">
                <el-input type="textarea" v-model="form.goodsDetail"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="update">立即修改</el-button>
                <el-button @click="quxiao">取消</el-button>
            </el-form-item>
        </el-form>
        </el-dialog>
        <input type="text" v-model="searchName">商品状态
        <select name="" id="" v-model="searchStatus">
            <option value="1">上架</option>
            <option value="0">下架</option>
        </select>
        <button @click="search">查询</button>
        <el-table
                :data="productList"
                height="550"
                border
                style="width: 100%">
            <el-table-column
                    label="商品图片"
                    width="180">
                <template slot-scope="scope">
                    <img :src="scope.row.goodsImgs" alt="">
                </template>
            </el-table-column>
            <el-table-column
                    prop="goodsName"
                    label="名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="goodsPrice"
                    label="价格">
            </el-table-column>
            <el-table-column
                    label="状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.status">上架</span>
                    <span v-else>下架</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="goodsDetail"
                    label="商品详情">
            </el-table-column>
            <el-table-column
                    label="创建时间">
                <template slot-scope="scope">
                    <span>{{scope.row.createTime | dateFormat}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.row.id,scope.row.goodsName,scope.row.goodsPrice,scope.row.goodsScore,scope.row.status,scope.row.recommend,scope.row.goodsDetail)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                    background
                    layout="prev, pager, next, jumper, ->, total"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="current"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'productList',
        data(){
            return {
                productList:[],
                total:0,//总条数
                current:1,//当前页
                pageSize:5,//每页条数
                searchName:"",
                searchStatus:"",
                dialogVisible: false,
                form: {
                    id:'',
                    name: '',
                    price: '',
                    score: '',
                    status: '',
                    recommend: '',
                    goodsDetail: ''
                }
            }
        },
        methods:{
            //模态框设置
            handleClose() {
                this.dialogVisible= false
            },
            quxiao:function () {
                this.dialogVisible = false;
            },
            //修改商品
            handleEdit(id,name,price,score,status,recommend,goodsDetail) {
                this.dialogVisible=true;
                this.form.id=id;
                this.form.name=name;
                this.form.price=price;
                this.form.score=score;
                this.form.goodsDetail=goodsDetail;

            },
            update:function () {
                var id = this.form.id;
                var name = this.form.name;
                var price = this.form.price;
                var score = this.form.score;
                var status = this.form.status;
                var recommend = this.form.recommend;
                var goodsDetail = this.form.goodsDetail;
                this.$axios.post('/api/goods/edit', {
                        "id": id,// 商品ID-必传
                        "brandId": "fbca81a68f6611eaa03e0242ac110002",// 品牌ID-必传
                        "catId": "4ff6bcab95b811eaa03e0242ac110002",// 分类ID-必传
                        "goodsDetail": goodsDetail,// 商品详情-必传
                        "goodsImgs": "https://dss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1016887494,1066933000&fm=111&gp=0.jpg",// 商品图片-必传
                        "goodsName": name,// 名字-必传
                        "goodsPrice": price, // 商品价格-必传
                        "goodsScore": score,// 商品积分-必传
                        "goodsTitle": "小蜜蜂-扩音器", // 商品标题-必传
                        "keywords": "扩音器,喇叭", // 关键字-必传
                        "recommend": recommend, // 是否推荐,1 推荐, 0 不推荐-必传
                        "status": status// 状态, 1 上架, 0 下架-必传
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                });
                this.dialogVisible=false;
            },
            //删除
            handleDelete(id) {
                this.$axios.post('/api/goods/delete', {
                        id:id
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //分页设置
            handleCurrentChange(val) {
                var pageSize = this.pageSize;
                this.$axios.post('/api/goods/list', {
                        "page": val,
                        "pageSize":pageSize,
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                        console.log(res);
                        this.productList = res.data.data.list;
                        this.total = res.data.data.total;
                    }
                );
                console.log(`当前页: ${val}`);
            },
            //搜索
            search:function () {
                var Name = this.searchName;
                var Status = this.searchStatus;
                this.$axios.post('/api/goods/list', {
                        "goodsName": Name,
                        "status": Status
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.productList = res.data.data.list;
                    this.total = res.data.data.total;
                    this.pageSize = res.data.data.pageSize;
                    }
                );
            }
        },
        mounted:function(){
            this.$axios.post('/api/goods/list', {
                    "page": 1,
                    "pageSize": 5,
                },
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            ).then((res) => {
                    console.log(res);
                    this.productList = res.data.data.list;
                    console.log(this.productList);
                    this.total = res.data.data.total;
                    this.pageSize = res.data.data.pageSize;
                    this.$message.success(res.data.msg);
                }
            )
      }
    }
</script>

<style scoped>
    #productList {
        position: relative;
    }
    #addProduct {
        position: absolute;
        z-index: 2;
        background-color: #fff;
    }

    #addProduct p {
        width: 400px;
    }
    img {
        width: 120px;
    }

</style>
