<template>
    <div id="adminList">
        管理员列表
        <el-dialog
                title="修改用户名"
                :visible.sync="userDialog"
                width="30%"
                :before-close="handleClose">
            <el-form ref="form" :model="userForm" label-width="80px">
                <el-form-item label="新用户名">
                    <el-input v-model="userForm.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="userEdit">立即修改</el-button>
                    <el-button @click="quxiao">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <el-dialog
                title="修改密码"
                :visible.sync="pwdDialog"
                width="30%"
                :before-close="handleClose">
            <el-form ref="form" :model="pwdForm" label-width="80px">
                <el-form-item label="新密码">
                    <el-input v-model="pwdForm.newPwd"></el-input>
                </el-form-item>
                <el-form-item label="旧密码">
                    <el-input v-model="pwdForm.oldPwd"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="pwdEdit">立即修改</el-button>
                    <el-button @click="quxiao">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog
                title="添加管理员"
                :visible.sync="addDialog"
                width="30%"
                :before-close="handleClose">
            <el-form ref="form" :model="form" label-width="90px">
                <el-form-item label="管理员名称">
                    <el-input v-model="adminName"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="add">立即创建</el-button>
                    <el-button @click="quxiao">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-button type="primary" @click="addUser">添加管理员</el-button>
        <el-table
                :data="adminList"
                height="550"
                border
                style="width: 100%">
            <el-table-column
                    prop="name"
                    label="用户名"
                    width="180">
            </el-table-column>
            <el-table-column
                    label="状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.status">正常</span>
                    <span v-else>禁用</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="上次登录">
                <template slot-scope="scope">
                    <span>{{scope.row.lastLoginTime | dateFormat}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="创建时间">
                <template slot-scope="scope">
                    <span>{{scope.row.createTime | dateFormat}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="user(scope.row.id)">修改用户名</el-button>
                    <el-button
                            size="mini"
                            @click="pwd(scope.row.id)">修改密码</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                    background
                    layout="prev, pager, next, jumper, ->, total"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="current"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'adminList',
        data(){
            return {
                adminList:[],
                total:0,//总条数
                current:1,//当前页
                pageSize:5,//每页条数
                userDialog: false,
                pwdDialog: false,
                addDialog: false,
                adminName:'',
                userForm: {
                    id:'',
                    name:''
                },
                pwdForm: {
                    id:'',
                    newPwd:'',
                    oldPwd:''
                }
            }
        },
        methods:{
            //添加管理员
            add:function () {
                var name = this.adminName;
                this.$axios.post('/api/admin/add', {
                        "admin": {
                            "name": name // 管理员名字-必传
                        },
                        "adminRoles": [] // 管理员角色数据, 传递空数组即可-必传
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //模态框设置
            handleClose() {
                this.userDialog= false;
                this.pwdDialog = false;
                this.addDialog = false;
            },
            quxiao:function () {
                this.userDialog = false;
                this.pwdDialog = false;
            },
            user(id){
                this.userDialog= true;
                this.userForm.id = id;
            },
            addUser(){
                this.addDialog= true;
            },
            pwd(id){
                this.pwdDialog= true;
                this.pwdForm.id = id;
            },
            //删除管理员
            handleDelete(id) {
                this.$axios.post('/api/admin/delete', {
                        id:id
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //修改用户名
            userEdit() {
                this.$axios.post('/api/admin/edit', {
                        "admin": {
                            "id": this.userForm.id, // 管理员ID-必传
                            "name": this.userForm.name // 修改的名字-必传
                        },
                        "adminRoles": []  // 管理员角色数据, 传递空数组即可-必传
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //修改密码
            pwdEdit() {
                this.$axios.post('/api/admin/change/pwd', {
                        "id": this.pwdForm.id,// 管理员ID-必传
                        "newPwd": this.pwdForm.newPwd, // 新密码-必传
                        "oldPwd": this.pwdForm.oldPwd // 旧密码-必传
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                    console.log(res);
                    this.$message.success(res.data.msg);
                    this.$router.go(0);
                })
            },
            //分页设置
            handleCurrentChange(val) {
                var pageSize = this.pageSize;
                this.$axios.post('/api/admin/list', {
                        "page": val,
                        "pageSize":pageSize,
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                ).then((res) => {
                        console.log(res);
                        this.adminList = res.data.data.list;
                        this.total = res.data.data.total;
                    }
                );
                console.log(`当前页: ${val}`);
            }
        },
        //挂载后获取数据
        mounted:function () {
            this.$axios.post('/api/admin/list', {

                    "page": 1,// 当前页-必传
                    "pageSize": 5,// 页容量-必传
                },
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            ).then((res) => {
                console.log(res);
                this.adminList = res.data.data.list;
                console.log(this.adminList);
                this.total = res.data.data.total;
                this.pageSize = res.data.data.pageSize;
                this.$message.success(res.data.msg);
            })
        }
    }
</script>

<style scoped>

</style>
