<template>
    <div id="login">
        <div>
            <form action="">
                <h1>登录</h1>
                <p>欢迎来到后台系统，<br> 请登录：</p>
                <p>用户名</p>
                <p><el-input v-model="user" placeholder="请输入内容" clearable></el-input></p>
                <p>密码</p>
                <p><el-input placeholder="请输入密码" v-model="pwd" show-password></el-input></p>
                <p><el-checkbox v-model="checked">记住密码</el-checkbox></p>
                <el-button round type="button"  id="btnLogin" @click="login">登录</el-button>
            </form>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'login',
        data(){
            return {
                user:'',
                pwd:'',
                checked:false
            }
        },
        methods: {
            login: function () {
                var LoginID = this.user;
                console.log(LoginID);
                var LoginPwd = this.pwd;
                this.$axios.post('/api/admin/login', {
                        name: LoginID, //  会员名字, 如果传递 模糊查询
                        password: LoginPwd // 会员电话, 如果传递 模糊查询
                    },
                    {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    }
                    ).then((res) => {
                    console.log(res);
//                    const { data: resp } = res
                    if (res.data.code !== 200) {
                        return this.$message.error('登录失败')
                    } else {
                        this.$message.success('登录成功');
                        this.$router.push('Home');
                    }
                })
            }

        }

    }
</script>

<style scoped>
    * {
        margin: 0;
        padding: 0;
    }
    #login {
        height:100%;
        width:100%;
        position: relative;
    }
    #login>div {
        width:900px;
        height:600px;
        background:rgba(255,255,255,1);
        box-shadow:0 2px 19px 1px rgba(69,47,133,0.2);
        box-sizing: border-box;
        padding: 60px;
        position: absolute;
        /*top: calc(50% - 300px);*/
        left: calc(50% - 450px);
    }
    button {
        width:400px;
        height:40px;
        background:rgba(253,106,48,1) !important;
        color: white !important;
    }
    p {
        width: 400px;
        margin-top: 20px;
        font-size:16px;
        font-family:Source Han Sans CN;
        font-weight:400;
        color:rgba(148,148,148,1);
        line-height:22px;
    }
    h1 {
        font-size:30px;
        font-family:Source Han Sans CN;
        font-weight:400;
        color:rgba(66,66,66,1);
        line-height:45px;
    }
</style>
